import { useEffect } from "react";
import { change } from "redux-form";
import { useNavigate } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "redux/hooks";
import BannerService from "services/banner.service";
import { bannerActions } from "redux/slices/banner/bannerSlice";

export default function AddHomePageBannerForm({ id }: any) {
  const form = "AddHomePageBannerForm";
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const banner = useAppSelector((state) => state.banner.banner);

  useEffect(() => {
    BannerService.getBannerbyID(`${id}`, dispatch);
    return () => {
      dispatch(bannerActions.setBanner(null));
    };
  }, [id, dispatch]);

  useEffect(() => {
    if (!banner || banner?.data === "Not Found") return;
    const {
      isPublished,
      redirectToReferrerPage,
      image,
      displayOrder,
      type,
      product,
      category,
      url,
    } = banner;
    dispatch(change(form, "isPublished", isPublished));
    dispatch(change(form, "redirectToReferrerPage", redirectToReferrerPage));
    dispatch(change(form, "image", image));
    dispatch(change(form, "displayOrder", displayOrder));
    dispatch(change(form, "type", type));
    dispatch(change(form, "product", product));
    dispatch(change(form, "category", category));
    dispatch(change(form, "url", url));
  }, [banner, navigate, dispatch]);

  return null;
}
