import ReduxFormFields from "components/molecules/ReduxFormFields";
import { Field, reduxForm } from "redux-form";
import Button from "components/atoms/Button";
import { fields } from ".";
import { Grid } from "@mui/material";
import { useAppSelector } from "redux/hooks";
import { required } from "utils/validate.util";
import SelectRedux from "components/molecules/SelectRedux";
import InputRedux from "components/molecules/InputRedux";
import SelectProduct from "components/organisms/SelectProduct";
import SelectCategories from "components/organisms/SelectCategories";

const AddHomePageBannerForm = ({ handleSubmit }: any) => {
  const form = "AddHomePageBannerForm";
  const banner_type = useAppSelector(
    (state) => state.form?.[form]?.values?.type
  );
  return (
    <form onSubmit={handleSubmit}>
      <Grid container spacing={2}>
        <Grid item md={12}>
          <ReduxFormFields fields={fields} />
        </Grid>
        {banner_type === "PRODUCT_DETAIL" ? (
          <Grid item md={12}>
            <Field
              name={"product"}
              label={"Product"}
              validate={[required]}
              component={SelectProduct}
            />
          </Grid>
        ) : banner_type === "CATEGORY" ? (
          <Grid item md={12}>
            <Field
              name={"category"}
              label={"Category"}
              validate={[required]}
              component={SelectCategories}
            />
          </Grid>
        ) : banner_type === "BLANK_PAGE" ? (
          <Grid item md={12}>
            <Field
              name={"url"}
              label={"Url"}
              validate={[required]}
              component={InputRedux}
            />
          </Grid>
        ) : (
          ""
        )}
      </Grid>
      <br />
      <Button color="primary" variant="contained" type="submit">
        Submit
      </Button>
    </form>
  );
};

export default reduxForm({
  form: "AddHomePageBannerForm",
})(AddHomePageBannerForm);
