import { Container } from "@mui/material";
import CircleLoader from "components/atoms/CircleLoader";
import GoBack from "components/atoms/GoBack";
import Banner from "components/templates/Banner";
import BasicCard from "components/templates/BasicCard";
import { useNavigate, useParams } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "redux/hooks";
import BannerService from "services/banner.service";
import AddHomePageBannerForm from "./AddHomePageBannerForm";
import UpdateHomePageBannerForm from "./UpdateHomePageBannerForm";

export default function AddHomePageBanner() {
  const { id } = useParams();
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const loading = useAppSelector((state) => state.formLoader.loading);

  const handleSubmit = (values: any) => {
    let data = { ...values };
    data.image = data.image?.images?.[0].path;
    if (id) BannerService.updateBanner(id, data, dispatch, navigate);
    else BannerService.createBanner(data, dispatch, navigate);
  };

  return (
    <Container maxWidth="lg">
      <GoBack path="/home-page-banner" title="HomePageBanner" />
      <Banner heading={`${id ? "Update" : "Add New"} Banner`}></Banner>
      <BasicCard>
        {loading && <CircleLoader />}
        <AddHomePageBannerForm onSubmit={handleSubmit} />
        {id && <UpdateHomePageBannerForm id={id} />}
      </BasicCard>
    </Container>
  );
}
